.md {
    padding: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: auto;
    height: 90%;
}

.edit {
    outline: none;
    font-size: 14px;
}

.edit-container  {
    width: 50%;
    height: 100%;
    padding: 5px 35px 50% 35px;
    overflow: auto;
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: normal;
    border: 1px solid #ccc;
    border-radius: 4px;
    color: #EBD1B7;
    background: #36312c;

}

.preview {
    width: 100%;
    height: 100%;
    padding: 5px 35px 50% 35px;
    overflow: auto;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.divider {
    width: 20px;
    height: 100%;
}


